////
/// Global Utilities
/// Animation
////

/* stylelint-disable indentation, rule-empty-line-before, number-no-trailing-zeros */

@keyframes svg-trace-stroke {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  60% {
    opacity: 1;
  }

  90% {
    transform: scale(1.4);
  }

  100% {
    opacity: 0;
  }
}

@keyframes pop {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  75% {
    opacity: 1;
    transform: scale(1.05);
  }
  100% {
    transform: scale(1.02);
  }
}

@keyframes shake {
  0% {
    transform: translate3d(0, 0, 0);
  }
  25% {
    transform: translate3d(0.6rem, 0, 0);
  }
  50% {
    transform: translate3d(-1rem, 0, 0);
  }
  75% {
    transform: translate3d(0.4rem, 0, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

