////
/// Draggable Theme
/// Cursor
////

@import 'utils/shared/functions';

$draggable-cursor-data: (
  base: #{path-to-asset(image, 'cursor-auto.png'),
  auto},
  text: #{path-to-asset(image, 'cursor-text.png'),
  text},
  pointer: (
    base: #{path-to-asset(image, 'cursor-pointer.png'),
    pointer},
    active: #{path-to-asset(image, 'cursor-pointer-clicked.png'),
    pointer},
  ),
  drag: (
    base: #{path-to-asset(image, 'cursor-drag.png'),
    auto},
    active: #{path-to-asset(image, 'cursor-drag-clicked.png'),
    auto},
  ),
  rock: (
    base: #{path-to-asset(image, 'cursor-rock.png'),
    pointer},
    active: #{path-to-asset(image, 'cursor-rock-clicked.png'),
    pointer},
  ),
);
